#autocomplete {
}

.hide-till-ready {
    visibility: hidden;
}
//when autocomplete widget is ready
.algolia-autocomplete .hide-till-ready {
    visibility: visible;
}

.algolia-autocomplete {
    width: 100%;
    border: 0;
}
.autocomplete-header .algolia-autocomplete {
    width: calc(100% - 60px);
    margin-left: 20px;
}
.algolia-autocomplete .aa-input::placeholder {
    color: #c0c9cf;
}
.algolia-autocomplete .aa-input,
.algolia-autocomplete .aa-hint {
    border-radius: 3px;
    width: 100%;
    padding: 18px 0 18px 48px;
    border: 0;
    font-size: 18px;
    box-sizing: border-box;
    border: 1px solid transparent;
    outline: none;
}
.algolia-autocomplete .aa-hint {
    // color: #C0C9CF;
    color: black;
    background-color: var(--gray-200) !important;
}

.autocomplete-header .algolia-autocomplete .aa-hint {
    background-color: var(--gray-100) !important;
}
.algolia-autocomplete .aa-input {
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: 21px center;
    transition: background-color 300ms, border-color 300ms;
}
.algolia-autocomplete .aa-input:focus {
    border-color: var(--gray-200);
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.05);
    background-color: rgba(255, 255, 255, 0.91) !important;
}

.autocomplete-header .algolia-autocomplete .aa-input {
    background-position: 11px center;
}
.autocomplete-header .algolia-autocomplete .aa-input,
.autocomplete-header .algolia-autocomplete .aa-hint {
    padding: 10px 0 10px 35px;
    font-size: 16px;
}
.autocomplete-header {
    flex-grow: 1;
}
@mixin algolia() {
    // background-image: url('https://www.algolia.com/static_assets/images/press/downloads/algolia-logo-light.svg');
    background-size: 50px 50px;
    background-repeat: no-repeat;
    background-position: right 15px top 0;
}
.algolia-autocomplete .aa-dropdown-menu {
    width: 100%;
    text-align: left;
    background-color: white;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
    margin-top: 6px;
    border-radius: 3px;
    max-height: 300px;
    overflow-y: scroll;
}
.algolia-autocomplete .aa-dropdown-menu .aa-suggestion {
    cursor: pointer;
    padding: 15px 20px;
}
.algolia-autocomplete .aa-dropdown-menu .aa-suggestion.aa-cursor {
    background-color: var(--gray-200);
}
.algolia-autocomplete .aa-dropdown-menu .aa-suggestion em {
    font-weight: bold;
    font-style: normal;
}
@media screen and (min-width: 560px) {
    .landing .algolia-autocomplete .aa-dropdown-menu {
        @include algolia();
    }
    .landing
        .algolia-autocomplete
        .aa-dropdown-menu
        .aa-suggestion:first-child.aa-cursor:not(.aa-notfound) {
        @include algolia();
    }
}

@media screen and(max-width: 712px) {
    .algolia-autocomplete .aa-input,
    .algolia-autocomplete .aa-hint {
        font-size: 16px;
    }

    .algolia-autocomplete .aa-dropdown-menu {
        font-size: 14px;
    }
}

.algolia-autocomplete .aa-notfound {
    font-size: 14px;
    color: var(--gray-600);
    background-image: none !important;
    display: flex;
    justify-content: space-between;
    background-color: white;

    em {
        color: var(--gray-800);
    }
}
